html, body {
  color: #333;
  background-color: #f0f0f0;
  height: 100vh;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Helvetica, Arial, sans-serif;
  overflow: hidden;
}

.editor-layout {
  box-sizing: border-box;
  grid-template-columns: 300px 1fr 100ch;
  gap: 8px;
  height: 100%;
  padding: 8px;
  transition: grid-template-columns .25s ease-in-out;
  display: grid;
}

#power-library-panel {
  background-color: #fff;
  border-radius: 8px;
  flex-direction: column;
  min-width: 0;
  display: flex;
  overflow-y: auto;
  box-shadow: 0 2px 4px #0000001a;
}

#power-list {
  flex-grow: 1;
  padding: 8px;
}

#main-content-panel {
  flex-direction: column;
  gap: 8px;
  min-width: 0;
  display: flex;
}

#controls {
  background: #fff;
  border-radius: 8px;
  flex-wrap: wrap;
  flex-shrink: 0;
  gap: 8px;
  padding: 8px;
  display: flex;
  box-shadow: 0 2px 4px #0000001a;
}

#controls button {
  cursor: pointer;
  background-color: #f7f7f7;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 6px 12px;
}

#controls button:hover {
  background-color: #e9e9e9;
  border-color: #bbb;
}

#blocklyDiv {
  flex-grow: 1;
  position: relative;
}

#code-container {
  color: #f8f8f2;
  box-sizing: border-box;
  white-space: pre-wrap;
  overflow-wrap: break-word;
  word-break: break-word;
  background-color: #272822;
  flex-grow: 1;
  margin: 0;
  padding: 1em;
  font-family: Fira Code, Dank Mono, monospace;
  font-size: 14px;
  line-height: 1.5;
  overflow: auto;
}

[x-cloak] {
  display: none !important;
}

.modal-overlay {
  z-index: 1000;
  background-color: #0009;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

.modal-content {
  z-index: 1001;
  background-color: #fff;
  border-radius: 12px;
  width: 90%;
  max-width: 500px;
  padding: 25px;
  transition: all .2s ease-out;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
  box-shadow: 0 5px 15px #0000004d;
}

.enter-start {
  opacity: 0;
  transform: translate(-50%, -50%)scale(.95);
}

.enter-end, .leave-start {
  opacity: 1;
  transform: translate(-50%, -50%)scale(1);
}

.leave-end {
  opacity: 0;
  transform: translate(-50%, -50%)scale(.95);
}

.editor-layout.library-collapsed {
  grid-template-columns: 45px 1fr 100ch;
}

.editor-layout.code-collapsed {
  grid-template-columns: 300px 1fr 40ch;
}

.editor-layout.library-collapsed.code-collapsed {
  grid-template-columns: 45px 1fr 40ch;
}

#code-preview-panel {
  background-color: #fff;
  border-radius: 8px;
  flex-direction: column;
  display: flex;
  overflow: hidden;
  box-shadow: 0 2px 4px #0000001a;
}

.panel-header {
  border-bottom: 1px solid #e0e0e0;
  flex-shrink: 0;
  justify-content: space-between;
  align-items: center;
  padding: 12px 16px;
  display: flex;
}

#power-library-panel h2 {
  border-bottom: none;
  margin: 0;
  padding: 0;
  font-size: 16px;
}

.code-header {
  color: #555;
  background-color: #f7f7f7;
  border-top: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
  padding: 4px 10px;
  font-size: 13px;
  font-weight: 500;
}

.toggle-btn {
  cursor: pointer;
  background: none;
  border: 1px solid #0000;
  border-radius: 4px;
  padding: 2px 6px;
  font-size: 16px;
  line-height: 1;
}

.toggle-btn:hover {
  background-color: #e0e0e0;
}
